#my-calendar thead {
  cursor: default;
}
#my-calendar thead td {
  min-width: 30px;
}
#my-calendar thead td[colspan] {
  text-align: center;
  font-weight: bold;
}
#my-calendar thead tr:last-child td {
  text-align: center;
}
#my-calendar tbody td,
#my-calendar tfoot td,
#my-calendar tbody span,
#my-calendar tfoot span {
  padding: 0;
  text-align: center;
  font-size: 14px;
}
#my-calendar tbody span,
#my-calendar tfoot span {
  display: inline-block;
  min-width: 40px;
}
#my-calendar tbody .is-payed,
#my-calendar tfoot .is-payed {
  color: white;
  background: green;
}
#my-calendar tbody .un-payed,
#my-calendar tfoot .un-payed {
  background: darkred;
  color: white;
}
#my-calendar tbody td.free,
#my-calendar tbody td.is-payed,
#my-calendar tbody td.un-payed {
  cursor: pointer;
}
#my-calendar tfoot td:first-child {
  text-align: right;
}
#my-calendar td {
  vertical-align: middle;
  text-align: center;
}
#my-calendar td.final {
  display: inline-flex;
  flex-direction: row;
}
.tooltip-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  cursor: pointer;
}
.tooltip-container.is-payed,
.tooltip-container.un-payed {
  min-width: 40px;
}
.total {
  cursor: pointer;
  min-width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
